@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300&family=Nanum+Gothic&display=swap');

@media only screen and (min-width: 1000px) {
    @keyframes digital_art {
        0% {
            transform: rotateY(0deg);
        }

        100% {
            transform: rotateY(-360deg);
        }
    }


    body {
        margin: 0;
        font-family: 'Mulish', sans-serif;
    }

    .header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: auto;
        border-bottom: 1px solid #abacb1;
        margin: 0px 200px 0px 200px;
    }

    .header_nav {
        display: flex;
        flex-direction: column;
        margin-left: 200px;
    }

    .header button {
        display: none;
    }

    .header_nav ul {
        list-style: none;
        display: flex;
        align-items: center;
        padding: 0;
        margin: 0;
    }

    .header_nav ul a {
        text-decoration: none;
    }

    .header_nav ul li {
        font-weight: bold;
        padding: 0px 20px 0px 20px;
        color: #252525;
    }

    .main_header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: auto;
        margin: 0px 200px 0px 200px;
    }

    .main_header div a {
        text-decoration: none;
    }

    .main_header div {
        display: flex;
        align-items: center;
    }

    .main_grid {
        display: grid;
        grid-template-areas:
            "A B C D";
        column-gap: 10px;
        row-gap: none;
        margin: 50px 200px 0px 200px;
        padding-bottom: 50px;
        border-bottom: 1px solid #abacb1;
    }

    .main_grid img {
        width: 368px;
        height: 273px;
        cursor: pointer;
    }

    .main_grid img:hover {
        transition: all 0.7s;
        filter: brightness(50%);
    }

    .seoul {
        grid: A;
        overflow: hidden;
    }

    .seoul .explanation button:last-child{
        margin:10px 0px 0px 0px
    }
    .seoul div:first-child {
        width: 368px;
        height: 273px;
        overflow: hidden;
    }

    .gachon {
        grid: B;
        overflow: hidden;
    }

    .gachon div:first-child {
        width: 368px;
        height: 273px;
        overflow: hidden;
    }

    .gachon .explanation p {
        margin: 0px 0px 35px 0px;
    }

    .ducsugung {
        grid: C;
        overflow: hidden;
    }

    .ducsugung div:first-child {
        width: 368px;
        height: 273px;
        overflow: hidden;
    }

    .chungju {
        grid: D;
        overflow: hidden;
    }

    .chungju div:first-child {
        width: 368px;
        height: 273px;
        overflow: hidden;
    }

    .chungju .explanation p {
        margin: 0px 0px 35px 0px;
    }


    .explanation {
        display: flex;
        flex-direction: column;
        color: #252525;
    }

    .explanation h2 {
        border-top: 1px solid #abacb1;
        margin: 10px 0px 15px 0px;
        padding-top: 15px;
    }

    .explanation p {
        margin: 0px 0px 15px 0px;
        font-size: 14.5px;
        font-weight: 600;
    }

    .explanation button {
        border-radius: 15px;
        border: 1px solid #252525;
        background-color: white;
        margin: 0px 5px 0px 5px;
        padding: 3px 10px 3px 10px;
        font-weight: bold;
        font-size: 14px;
        cursor: pointer;
    }

    .explanation button:hover {
        background-color: #4f5050;
        color: white;
        border: 1px solid #4f5050;
        transition: all 0.3s;
    }

    .show_header {
        display: flex;
